body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif !important;
  font-weight: 400 !important;
  font-size: 12pt !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-family: "Montserrat", sans-serif !important;
  font-weight: 500 !important;
}

h2 {
  font-family: "Montserrat", sans-serif !important;
  font-weight: 300 !important;
}


.nav-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 2% 5% 2% 5%;
}

.contact > .btn,
.contact > .btn-secondary {
  background-color: #fff;
  color: #7fda57;
  font-weight: 600;
  border: 1px solid #7fda57;
  border-radius: 10px;
}

.contact > .btn:hover {
  border: 1px solid #7fda57;
  background-color: #7fda57;
}

.home-wrapper {
  display: flex;
  flex-direction: column;
  /* align-items: flex-start; */
}

.home-wrapper > div {
  height: auto;
  /* border: 1px solid black; */
  text-align: center;
}

.intro {
  padding: 3%;
}

.intro > h1,
.intro > h2 {
  margin-bottom: 3%;
}

.skills {
  background: linear-gradient(#7fda57 50%, #fff 50%);
  color: #fff;
  padding: 3%;
}

.skills > h1 {
  margin-bottom: 3%;
}

.skills-card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 3%;
}

.skills-card > div {
  color: #7fda57;
  font-weight: 800;
  background-color: #fff;
  padding: 2%;
  min-width: 500px;
}

.skills-card > div > h2 {
  margin-bottom: 10%;
  margin-top: 5%;
}

.single-skills-card {
  list-style: none;
  padding: 3%;
}

.single-skills-card > li {
  padding: 3%;
}

.technical-skills {
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-left: 1px solid #7fda57;
  border-bottom: 1px solid #7fda57;
  border-right: 1px solid #7fda57;
}

.professional-skills {
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-right: 1px solid #7fda57;
  border-bottom: 1px solid #7fda57;
}

.icons {
  font-size: 48pt;
  color: #7fda57;
}

.work > h1 {
  margin-bottom: 3%;
}

.work > p {
  margin: 3%;
}

.box-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.box-wrapper > div {
  background-color: #7fda57;
  border-radius: 15px;
  color: #fff;
  padding: 3%;
  max-width: 600px;
  min-height: 400px;
}

.box-wrapper > div > h2 {
  margin-bottom: 3%;
}

.box-wrapper > div > h3 {
  margin-bottom: 8%;
  font-size: 18pt;
}

.box-wrapper > div > ul {
  list-style: none;
}

.box-wrapper > div > ul > li {
  margin-bottom: 5%;
}

.footer {
  margin-top: 6%;
  padding: 3%;
  background-color: #7fda57;
  color: #fff;
}

.social-icons > a {
  margin: 2%;
}
.copyright {
  margin-top: 2%;
}

